//POST
function post(url, datas, success, loading = false) {
	if (loading) {
		uni.showLoading({
			title: '',
			mask: true

		});
	}
	uni.request({
		url: rootDocment + url,
		method: 'POST',
		header: {
			'Accept': 'application/json',
			'content-type': 'application/json', //
			'token': uni.getStorageSync("userToken"),
		},
		data: datas,
		success: res => {
			success(res)
			console.log(res)
			if(res.data.code==401){
				uni.removeStorageSync('userToken');
				
				// uni.reLaunch({
				// 	url:'/pages/account/login'
				// })
			}
			// if (res.header.authorization != undefined) {
			// 	uni.setStorageSync("cdj_token", res.header.authorization)
			// }
			if (loading) {
				uni.hideLoading();
			}
		},
		fail: res => {
			uni.showModal({
				title: '',
				content: '',
				showCancel: false
			})
		},

	})

}
//
function needLogin(success) {
	if (uni.getStorageSync('userToken')) {
		success()
	} else {
		uni.showModal({
			title: '',
			content: '',
			cancelColor: '#999',
			confirmColor: "#59B727",
			success: function(res) {
				if (res.confirm) {
						getApp().globalData.isReload = true;
					uni.reLaunch({
						url: '/pages/account/login'
					})
				}
			}
		})
	}
}

function Toast(message) {
	uni.showToast({
		title: message,
		icon: 'none',
		duration: 2000
	})
}

function showModal(message,res) {
	uni.showModal({
		title:'',
		content:message,
		confirmColor:'#57B127',
		success: () => {
			res()
		}
		
		
	})
}

export {
	get,
	post,
	Toast,
	needLogin,
	showModal
}
